@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --primary-color: #222034;
  --background-color: #45283c;
  --freshjam-color: #bc1c5f;
  --notfreshjam-color: #74004a;
  --wood-color: #8f563b;
  --white-color: #cbeffc;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  
}

.App {
  text-align: center;
  background-color: black;
  color: #cbeffc;
  color: var(--white-color);
  height: 300vh;
}

.Welcome{
  background-color: #282c34;
  min-height: 100vh;
  /*display: flex;*/
  /*flex-direction: column;*/
  /*align-items: center;*/
  /* justify-content: center; */
  color: white;
  display: flex;
  margin:auto;
}

.pot{
  position: absolute;
  top: 50%;
  left: 50%;
  height: 500px;
  width: 650px;
  transform:  translateY(-50%) translateX(-50%);
}.piperight{
  position: absolute;
  top: 0px;
  right:0px;
  height: 466px;
  width: 204px;
}.pipeleft{
  position: absolute;
  top: 20%;
  left:0px;
  height: 500px;
  width: 100px;
}.gamejam{
  position: absolute;
  bottom: 0px;
  left: 50%;
  transform:  translateX(-50%);
  width: 500px;
}

@media screen and (max-width:700px){
  .pot{
    height: 62.5vw;
    width: 90vw;
  }
}@media screen and (max-height:500px){
  .pot{
    height: 62.5vh;
    width: 90vh;
  }
}

@media screen and (max-width:550px){
  .gamejam{
    width: 100%;
  }
  .pipeleft{
    display: none
  }
}@media screen and (max-height:700px){
  .pipeleft{
    display: none
  }
}
@media screen and (max-width:900px){
  .piperight{
    display: none;
  }
}
@media screen and (max-height:550px){
  .piperight{
    display: none;
  }

}
.svg-image{
  width: 100%;
  height: 100%;

}
.WhatIs{
    color: white;
    width: 100%;
    height: 100vh;
    animation: gradientmove 1s infinite;
    background-size: 200% 200%;
    background-position: 0% 100%;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-direction: alternate;
    background: linear-gradient(56deg, rgba(188,28,95,1) 0%, rgba(116,0,74,1) 50%, rgba(143,86,59,1) 100%);
  }
  
  @keyframes gradientmove{
    from {
      background-position: 0% 100%;
    }
    to {
      background-position: 100% 0%;
    }
  }
  .SectionTitle{
    font-family: 'Roboto', sans-serif;
    font-size: 64px;
    margin-bottom: -70px;
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    z-index: 1;
  }
  #WhatIsWithoutTitle{
    height:100%;
    width:100%;
    margin:0 auto;
  }
  
  .item{
    display:inline-block;
    vertical-align:top;
    height:50%;
    width:50%;
  }
  .item:nth-child(2){
    background: var(--notfreshjam-color);
    -webkit-filter: opacity(20);
            filter: opacity(20);
  }.item:nth-child(3){
    background: var(--freshjam-color);
    -webkit-filter: opacity(20);
            filter: opacity(20);
  }
  .item:nth-child(4){
    margin: 0;
  }
